
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { getAllBatch, getAllCategory, getAllUserProvince, save, myInfo } from '@/api/index'
import { errorMsg } from '@/utils/common'
import BScroll from 'better-scroll'
export default {
components: { Group, Selector, CheckIcon, PopupPicker, Checker, CheckerItem },
data () {
return {
provList: [],
cateList: [],
batchList: [],
province: [''],
category: [],
newCate: [],
batch: [],
value: [],
newVal: [],
provinceCode: '',
city: '',
address: '',
addressCode: '',
cityCode: '',
showAddress: false,
addressData: ChinaAddressV4Data,
showNewGK: false, // 
singleChoice: '', // 
multipleChoice: [''], // 
checkLincense: false,
completeScroll: null,
newGKList: ['110000', '310000', '330000'],
selectSubject: '',
location: '',
latitude: '', // 
longitude: '', // 
getAddr: ''
}
},
props: {
show: Boolean,
code: String
},
computed: {
openid () {
return this.$store.state.user.openid
},
pageType () {
return this.$route.query.type || ''
}
},














mounted () {
this.init()







if (this.pageType === 'edit' || this.pageType === 'predict') {
this.getUserInfo()
} else {
this.getList()
}
},
methods: {
gotoPage (name) {
if (name === -1) {
this.$router.go(-1)
} else {
this.$router.push({ name: name })
}
},
init () {
console.log('', this.$refs.completeContent)
this.$nextTick(() => {
this.completeScroll = new BScroll(this.$refs.completeContent, {
click: true
})
})
},
addClick () {
const _this = this
document.querySelector('.transCate').addEventListener('click', function () {
if (_this.value.length === 0) {
errorMsg(_this, '')
return false
}
_this.getAllCategory()
})
},
getAllCategory () {
getAllCategory(this.provinceCode).then(res => {
this.cateList = [res.data.data]
})
},
getLocation () {



const _this = this

wx.getLocation({
type: 'gcj02', // wgs84gpsopenLocation'gcj02'
success: function (res) {
_this.latitude = res.latitude
_this.longitude = res.longitude
console.log('', _this.latitude, _this.longitude)

var latLng = new qq.maps.LatLng(res.latitude, res.longitude)
_this.getAddr.getAddress(latLng)
},
cancel: function (res) {
_this.location = ''

return false


}
})



},
setWxjs () {
let url = encodeURIComponent(window.location.href.split('#')[0])


this.$axios.get('http://zhongkeruitong.top/score_analysis/wechat/getSign?url=' + url).then(res => {
if (res.data.code === 0) {
this.configContent = res.data.data
this.signWX()
}
})
},
signWX () {
let _this = this


wx.config({
debug: false, // ,apialertpclogpc
appId: this.configContent.appId, // 
timestamp: this.configContent.timestamp, // 
nonceStr: this.configContent.nonceStr, // 
signature: this.configContent.signature, // 
jsApiList: ['checkJsApi', 'openLocation', 'getLocation', 'updateAppMessageShareData', 'updateTimelineShareData'] 
})
wx.checkJsApi({
jsApiList: ['getLocation'],
success: function (res) {

if (res.checkResult.getLocation === false) {
alert('JS')
return false
}
}
})
wx.ready(function () {
wx.getLocation({
type: 'gcj02', // wgs84gpsopenLocation'gcj02'
success: function (res) {
_this.latitude = res.latitude
_this.longitude = res.longitude
console.log('', _this.latitude, _this.longitude)

var latLng = new qq.maps.LatLng(res.latitude, res.longitude)
_this.getAddr.getAddress(latLng)
},
cancel: function (res) {
_this.location = ''


return false


}
})
})
wx.error(function (res) {
console.log('js-sdk 000' + res.errMsg)

})

this.getAddr = new qq.maps.Geocoder({
complete: function (res) {
_this.address = res.detail
var allAddress = res.detail.addressComponents

var address = res.detail.address
_this.location = allAddress.province + allAddress.city + allAddress.district


_this.value = [allAddress.province + allAddress.city + allAddress.district]


console.log('', address, allAddress.province, allAddress.city, allAddress.district)

}
})
},
logHide (str) {



if (this.newVal.length > 0 && this.newVal.toString() !== this.value.toString()) {
this.$vux.toast.text('~')
this.category = []
this.batch = []
}
},
logShow (str) {
this.newVal = this.value
console.log('on-show', str)
console.log(this.newVal)
},
doShowAddress () {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
onShadowChange (ids, names) {

this.province = names[0]
this.city = names[1]
this.address = names[2]
this.provinceCode = ids[0]
this.addressCode = ids[2]
this.cityCode = ids[1]
if (this.newGKList.indexOf(ids[0]) > -1) {
this.showNewGK = true
} else {
this.showNewGK = false
}
console.log('', this.newVal, this.value)
},








changeCate (val) {


if (this.newCate[0].length > 0 && this.newCate.toString() !== this.category.toString()) {
this.$vux.toast.text('~')
this.batch = []
}
},
changeBatch (value) {
console.log(value)
},
showCategory () {
this.newCate = this.category

if (this.value.length === 0) {
errorMsg(this, '')
return
}
this.getAllCategory()
},
showBatch () {
console.log('', this.category === '')
console.log('', this.category)
if (this.value.length === 0) {
errorMsg(this, '')
return
}
if (this.category.length === 0) {
errorMsg(this, '')
return
}
getAllBatch(this.provinceCode, this.category[0]).then(res => {
this.batchList = [res.data.data]
})
},
onShowPro () {

},
toggleLincese (flag) {
this.$vux.confirm.show({
content: '',
onConfirm: () => {
this.checkLincense = true
}
})
},
submit () {
console.log('address', this.value)
this.selectSubject = ''
if (this.showNewGK === true) {
this.selectSubject += this.singleChoice
for (let i in this.multipleChoice) {
this.selectSubject += ',' + this.multipleChoice[i]
}
}
console.log('2222222', this.selectSubject, this.selectSubject.length)
console.log('', this.singleChoice, this.multipleChoice)
const prov = this.provinceCode

const category = this.category
const batch = this.batch
if (this.value.length === 0) {
errorMsg(this, '')
return
}
if (category.length === 0) {
errorMsg(this, '')
return
}
if (batch.length === 0) {
errorMsg(this, '')
return
}
if (!this.checkLincense) {
errorMsg(this, '')
return
}
console.log('geihou', this.selectSubject.length)

save({
openid: this.openid,
province: prov,
category: category[0],
batch: batch[0],
subject: this.selectSubject,
city: this.city,
address: this.address,
citycode: this.cityCode,
addresscode: this.addressCode
}).then(res => {

if (res.data.code === 0) {
if (this.pageType === 'predict') {
this.$router.push({ name: 'predict' })
} else {
this.$router.push({ name: 'my' })
}
} else {
errorMsg(this, res.data.msg)
}
})
},
getList () {
getAllUserProvince().then(res => {
this.provList = [res.data.data]
})
},
getUserInfo () {
myInfo(this.openid).then(res => {
console.log('', res.data)
this.checkLincense = true
if (res.data.code === 0) {
this.batch = [res.data.data.batch || '']
this.category = [res.data.data.category || '']
this.value.push(res.data.data.provincecode)
this.value.push(res.data.data.citycode)
this.value.push(res.data.data.addresscode)


}
this.getList()
})
}
}
}



